<template>
  <div>
      <ul>
        <li v-for="(item,index) in this.itemList" :key=item.itemName class="shadow">
            <span class="simpleImageBox">
              <img :src="require(`@/assets/images/${item.itemName}.jpg`)" class="simpleImage" />
            </span>
            <h3 class="contentTitle">
              itemName:
                {{item.itemName}},
                count:
                {{item.count}}
            </h3>
         
            <a href="http://localhost:3000/home/" class="playBtn">
              <font-awesome-icon :icon="['fas','play']"  @click="postData(item.itemName,index)"/>
            </a>
        
        </li>
      </ul>
  </div>
</template>

<script>
import axios from 'axios';
export default {

    data() {
        return{
            itemList:[],
            userList:[{uid:"jhon",sex:"남",age:15},
            {uid:"diana",sex:"여",age:24},
            {uid:"ryze",sex:"남",age:43},
            {uid:"aaron",sex:"남",age:50},
            {uid:"hailee",sex:"여",age:39},
            {uid:"eddie",sex:"남",age:52},],
            userOne:'',
        }
    },
    methods:{
      //서버에서 데이터 수신 getData()
      // getData() {
      //   //서버 url 입력 get(url)https://4d525421-d1de-468b-afa9-add953f0ad89.mock.pstmn.io/list"
      //   axios.get("http://localhost:3000/getItem/")
      //   .then((res) => {
      //     console.log(res.data);
      //     this.itemList=res.data
      //   })
      //   .catch(error =>{
      //     console.log(error);
      //   })
      //   .finally(()=>{
      //     this.listSort()
      //   })
   
      // },
      async getData() {
        try {
          const response =await axios.get("http://localhost:3000/getItem/")
          this.itemList=response.data
        } catch(error){
          console(error);
        } finally {
          this.listSort()
        }
      },

      //버튼 크릭시 유저데이터 송신 (데이터 폼)
      // postData(name,count,index) {
      //   console.log(name,count,index)
      //   axios.post('http://localhost:3000/get',{itemName:name, user:this.userOne})
      //   .then((res) =>{
      //     console.log(res)
      //     console.log(2)
      //   })
      //   .catch((error)=> {
      //     console.log(error)
      //   })
      //   .finally(()=> {

      //   })

      //     this.itemList[index].count+=1
      //     //리스트 count에 따른 정렬
      //     this.listSort()
      // },
      async postData(name,index){
        try{
          const response = await axios.post('http://localhost:3000/get',{itemName:name, user:this.userOne})
          console.log(response)
        } catch(error) {
          console.log(error)
        }
        this.itemList[index].count+=1
        this.listSort()
      },

      //리스트 정렬 내림차순
      listSort() {
        this.itemList.sort(function (a,b) {
          if(a.count>b.count){
            return -1;
          }
          if(a.count<b.count){
            return 1;
          }
          return 0;
        })
      }

      
    },
      created() {
      //서버 연결 코드 부분
      this.getData();
      this.userOne=this.userList[Math.floor(Math.random()*6)]

    },
}
</script>

<style scoped>
ul {
  list-style-type: none;
  padding-left: 0px;
  margin-top: 0;
  text-align: left;
}
li {
  display: flex;
  min-height: 125px;
  height: 125px;
  line-height: 125px;
  margin: 0.5rem 0;
  padding: 0 0.9rem;
  background: white;
  border-radius: 20px;
}
.simpleImageBox{
  /* line-height: 125px; */
  display: flex;
  width: 80px;
  height: 100px;
  line-height: 100px;
  margin-top:0.7rem;
  margin-right:0.9rem;

}
.simpleImage{
  object-fit: cover;
  height: 100%;
  width: 100%;

}
.playBtn{
  display: flex;
  margin-left: auto;
  margin-right: 0;
  margin-top:2.5rem;
  border: 0;
  outline: 0;
  padding:0.5rem,0;
  transition: all 0.2s;
  color: #778899;
  font-size:45px;
}
.playBtn:link {
  color: #778899;
}
.playBtn:hover{
  color: black;
}
.playBtn:visited{
  text-decoration: none;
}
.contentTitle{
  font-weight:20px;
  margin:0;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
}

</style>